//./components/UpStore.js
import React, { useEffect } from 'react';
import { connect } from 'react-redux';
import { addItem,addList,addReport } from '../actions';
import fetchData from './ReqServer';
import LoadServer from './LoadServer';
import LoadReport from './LoadReport';

const UpStore = ({ items,lists,reports,addItem,addList,addReport }) => {
    useEffect(()=>{
        fetchData()
            .then(marketListData => {
                addItem(marketListData);
            })
            .catch(error => {
                console.error('Error:', error);
            });
            LoadServer()
            .then(response => {
                response.map(item=>addList(item))
            })
            .catch(error => {
                console.error('Error:', error);
            });
            LoadReport()
            .then(response => {
                response.map(item=>addReport(item))
                // addReport(response);
                // console.log(response);
    })
            .catch(error => {
                console.error('Error:', error);
            });
            // response.data.map(item=>addList(item))
            
        },[])
          

    const handleShowitem=()=>{
      console.log(items);
      console.log(lists);
      console.log(reports);
    }
    return (
        <div>
            {/* <button onClick={handleAdditem}>gotostore</button> */}
            {/* <button onClick={handleShowitem}>show</button> */}
        </div>
    );
};
const mapStateToProps = (state) => ({
  items: state.items,
  lists: state.lists,
  reports: state.reports,
});
export default connect(mapStateToProps, {addItem,addList,addReport})(UpStore);

